import React, { useState } from 'react';
import axios from 'axios';
import './AuthPages.css';

/**
 * 
 */
function PasswordResetPage() {
  const [step, setStep] = useState(1); // 1: , 2: 
  const [email, setEmail] = useState('');
  const [token, setToken] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [loading, setLoading] = useState(false);

  // 1: 
  const handleRequestReset = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    setLoading(true);

    try {
      const response = await axios.post('/api/auth/password-reset/request', {
        email: email
      });

      if (response.data.success) {
        setSuccess(response.data.message);
        setStep(2); // 
      } else {
        setError(response.data.message);
      }
    } catch (err) {
      if (err.response && err.response.data) {
        setError(err.response.data.message || '');
      } else {
        setError('');
      }
    } finally {
      setLoading(false);
    }
  };

  // 2: 
  const handleConfirmReset = async (e) => {
    e.preventDefault();
    setError('');
    setSuccess('');

    // 
    if (newPassword !== confirmPassword) {
      setError('');
      return;
    }

    // 
    if (newPassword.length < 6) {
      setError('6');
      return;
    }

    setLoading(true);

    try {
      const response = await axios.post('/api/auth/password-reset/confirm', {
        token: token,
        newPassword: newPassword
      });

      if (response.data.success) {
        setSuccess('');
        // 3
        setTimeout(() => {
          window.location.href = '/login';
        }, 3000);
      } else {
        setError(response.data.message);
      }
    } catch (err) {
      if (err.response && err.response.data) {
        setError(err.response.data.message || '');
      } else {
        setError('');
      }
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="auth-container">
      <div className="auth-card">
        <h2 className="auth-title"></h2>

        {error && (
          <div className="auth-error">
            {error}
          </div>
        )}

        {success && (
          <div className="auth-success">
            {success}
          </div>
        )}

        {step === 1 ? (
          // 1: 
          <form onSubmit={handleRequestReset} className="auth-form">
            <p className="auth-description">
              
            </p>

            <div className="form-group">
              <label htmlFor="email"></label>
              <input
                type="email"
                id="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
                placeholder=""
                disabled={loading}
              />
            </div>

            <button
              type="submit"
              className="auth-button"
              disabled={loading}
            >
              {loading ? '...' : ''}
            </button>

            <div className="auth-links">
              <a href="/login"></a>
            </div>
          </form>
        ) : (
          // 2: 
          <form onSubmit={handleConfirmReset} className="auth-form">
            <p className="auth-description">
              
            </p>

            <div className="form-group">
              <label htmlFor="token"></label>
              <input
                type="text"
                id="token"
                value={token}
                onChange={(e) => setToken(e.target.value)}
                required
                placeholder=""
                disabled={loading}
              />
            </div>

            <div className="form-group">
              <label htmlFor="newPassword"></label>
              <input
                type="password"
                id="newPassword"
                value={newPassword}
                onChange={(e) => setNewPassword(e.target.value)}
                required
                placeholder="6"
                disabled={loading}
              />
            </div>

            <div className="form-group">
              <label htmlFor="confirmPassword"></label>
              <input
                type="password"
                id="confirmPassword"
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
                required
                placeholder=""
                disabled={loading}
              />
            </div>

            <button
              type="submit"
              className="auth-button"
              disabled={loading}
            >
              {loading ? '...' : ''}
            </button>

            <div className="auth-links">
              <a href="/login"></a>
            </div>
          </form>
        )}
      </div>
    </div>
  );
}

export default PasswordResetPage;
