import React, { useState, useEffect } from 'react';
import axios from 'axios';
import './CommentSection.css';

function CommentSection({ postId, boardId, isAnonymous }) {
  const [comments, setComments] = useState([]);
  const [loading, setLoading] = useState(true);
  const [newComment, setNewComment] = useState('');
  const [replyTo, setReplyTo] = useState(null);
  const [replyContent, setReplyContent] = useState('');
  const [editingId, setEditingId] = useState(null);
  const [editContent, setEditContent] = useState('');
  const [likedComments, setLikedComments] = useState({});

  // 
  const user = JSON.parse(localStorage.getItem('user') || '{}');

  useEffect(() => {
    fetchComments();
  }, [postId]);

  const fetchComments = async () => {
    try {
      const response = await axios.get(`/api/comments/post/${postId}`);
      setComments(response.data);

      // Check like status for each comment
      const user = JSON.parse(localStorage.getItem('user') || '{}');
      const likeStatuses = {};
      for (const comment of response.data) {
        try {
          const likeCheck = await axios.get(`/api/comments/${comment.id}/like/check`, {
            params: { userId: user.id || null }
          });
          likeStatuses[comment.id] = likeCheck.data.liked;
        } catch (e) {
          likeStatuses[comment.id] = false;
        }
      }
      setLikedComments(likeStatuses);

      setLoading(false);
    } catch (error) {
      console.error(':', error);
      setComments([]);
      setLoading(false);
    }
  };

  const handleSubmitComment = async (e) => {
    e.preventDefault();
    if (!newComment.trim()) return;

    // Get current user
    const user = JSON.parse(localStorage.getItem('user') || '{}');
    if (!user.id) {
      alert('');
      return;
    }

    try {
      const commentData = {
        postId: parseInt(postId),
        userId: user.id,
        content: newComment,
        anonymousId: isAnonymous ? `${Math.floor(Math.random() * 1000)}` : null
      };

      await axios.post('/api/comments', commentData);
      setNewComment('');
      fetchComments();
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  const handleSubmitReply = async (e, parentId) => {
    e.preventDefault();
    if (!replyContent.trim()) return;

    // Get current user
    const user = JSON.parse(localStorage.getItem('user') || '{}');
    if (!user.id) {
      alert('');
      return;
    }

    try {
      const replyData = {
        postId: parseInt(postId),
        userId: user.id,
        parentId: parentId,
        content: replyContent,
        anonymousId: isAnonymous ? `${Math.floor(Math.random() * 1000)}` : null
      };

      await axios.post('/api/comments', replyData);
      setReplyContent('');
      setReplyTo(null);
      fetchComments();
    } catch (error) {
      console.error(':', error);
      alert('');
    }
  };

  const handleLikeComment = async (commentId) => {
    try {
      const user = JSON.parse(localStorage.getItem('user') || '{}');
      const response = await axios.post(`/api/comments/${commentId}/like`, null, {
        params: { userId: user.id || null }
      });

      // Update liked state locally
      setLikedComments(prev => ({
        ...prev,
        [commentId]: response.data.liked
      }));

      // Update comment like count locally
      setComments(prev => prev.map(comment => {
        if (comment.id === commentId) {
          return {
            ...comment,
            likeCount: response.data.liked
              ? (comment.likeCount || 0) + 1
              : Math.max(0, (comment.likeCount || 0) - 1)
          };
        }
        return comment;
      }));
    } catch (error) {
      console.error(':', error);
    }
  };

  const handleDeleteComment = async (commentId) => {
    if (!window.confirm('')) return;

    if (!user.id) {
      alert('');
      return;
    }

    try {
      await axios.delete(`/api/comments/${commentId}?userId=${user.id}`);
      fetchComments();
    } catch (error) {
      console.error(':', error);
      if (error.response && error.response.status === 403) {
        alert('');
      } else {
        alert('');
      }
    }
  };

  // 
  const handleStartEdit = (comment) => {
    setEditingId(comment.id);
    setEditContent(comment.content);
  };

  // 
  const handleCancelEdit = () => {
    setEditingId(null);
    setEditContent('');
  };

  // 
  const handleSaveEdit = async (commentId) => {
    if (!editContent.trim()) return;

    if (!user.id) {
      alert('');
      return;
    }

    try {
      await axios.put(`/api/comments/${commentId}?userId=${user.id}`, {
        content: editContent
      });
      setEditingId(null);
      setEditContent('');
      fetchComments();
    } catch (error) {
      console.error(':', error);
      if (error.response && error.response.status === 403) {
        alert('');
      } else {
        alert('');
      }
    }
  };

  // 
  const isOwnComment = (comment) => {
    return user.id && comment.userId === user.id;
  };

  const getTimeAgo = (dateString) => {
    const now = new Date();
    const past = new Date(dateString);
    const diffInMinutes = Math.floor((now - past) / (1000 * 60));

    if (diffInMinutes < 1) return '';
    if (diffInMinutes < 60) return `${diffInMinutes}`;
    if (diffInMinutes < 1440) return `${Math.floor(diffInMinutes / 60)}`;
    if (diffInMinutes < 10080) return `${Math.floor(diffInMinutes / 1440)}`;
    return past.toLocaleDateString('ja-JP');
  };

  const topLevelComments = comments.filter(c => !c.parentId);
  const getReplies = (parentId) => comments.filter(c => c.parentId === parentId);

  if (loading) {
    return <div className="comments-loading">...</div>;
  }

  return (
    <div className="comments-section">
      <div className="comments-header">
        <h3> {comments.length}</h3>
      </div>

      <form className="comment-form" onSubmit={handleSubmitComment}>
        <textarea
          value={newComment}
          onChange={(e) => setNewComment(e.target.value)}
          placeholder="..."
          rows="3"
        />
        <button type="submit" className="submit-comment-btn">
          
        </button>
      </form>

      <div className="comments-list">
        {topLevelComments.length === 0 ? (
          <div className="no-comments">
            
          </div>
        ) : (
          topLevelComments.map(comment => (
            <div key={comment.id} className="comment-item">
              <div className="comment-header">
                <span className="comment-author">
                  {isAnonymous ? comment.anonymousId || '' : (comment.authorNickname || 'Unknown User')}
                </span>
                <span className="comment-time">{getTimeAgo(comment.createdAt)}</span>
              </div>

              {editingId === comment.id ? (
                <div className="comment-edit-form">
                  <textarea
                    value={editContent}
                    onChange={(e) => setEditContent(e.target.value)}
                    rows="3"
                  />
                  <div className="edit-actions">
                    <button onClick={handleCancelEdit}></button>
                    <button onClick={() => handleSaveEdit(comment.id)}></button>
                  </div>
                </div>
              ) : (
                <div className="comment-content">
                  {comment.content}
                </div>
              )}

              <div className="comment-actions">
                <button
                  className={`comment-action-btn ${likedComments[comment.id] ? 'liked' : ''}`}
                  onClick={() => handleLikeComment(comment.id)}
                >
                  {likedComments[comment.id] ? '' : ''} {comment.likeCount || 0}
                </button>
                <button
                  className="comment-action-btn"
                  onClick={() => setReplyTo(replyTo === comment.id ? null : comment.id)}
                >
                   
                </button>
                {isOwnComment(comment) && !comment.isDeleted && (
                  <>
                    <button
                      className="comment-action-btn edit"
                      onClick={() => handleStartEdit(comment)}
                    >
                       
                    </button>
                    <button
                      className="comment-action-btn delete"
                      onClick={() => handleDeleteComment(comment.id)}
                    >
                       
                    </button>
                  </>
                )}
              </div>

              {replyTo === comment.id && (
                <form
                  className="reply-form"
                  onSubmit={(e) => handleSubmitReply(e, comment.id)}
                >
                  <textarea
                    value={replyContent}
                    onChange={(e) => setReplyContent(e.target.value)}
                    placeholder="..."
                    rows="2"
                  />
                  <div className="reply-actions">
                    <button type="button" onClick={() => setReplyTo(null)}>
                      
                    </button>
                    <button type="submit"></button>
                  </div>
                </form>
              )}

              {getReplies(comment.id).map(reply => (
                <div key={reply.id} className="comment-item reply">
                  <div className="comment-header">
                    <span className="comment-author">
                      {isAnonymous ? reply.anonymousId || '' : (reply.authorNickname || 'Unknown User')}
                    </span>
                    <span className="comment-time">{getTimeAgo(reply.createdAt)}</span>
                  </div>

                  {editingId === reply.id ? (
                    <div className="comment-edit-form">
                      <textarea
                        value={editContent}
                        onChange={(e) => setEditContent(e.target.value)}
                        rows="2"
                      />
                      <div className="edit-actions">
                        <button onClick={handleCancelEdit}></button>
                        <button onClick={() => handleSaveEdit(reply.id)}></button>
                      </div>
                    </div>
                  ) : (
                    <div className="comment-content">
                      {reply.content}
                    </div>
                  )}

                  <div className="comment-actions">
                    <button
                      className={`comment-action-btn ${likedComments[reply.id] ? 'liked' : ''}`}
                      onClick={() => handleLikeComment(reply.id)}
                    >
                      {likedComments[reply.id] ? '' : ''} {reply.likeCount || 0}
                    </button>
                    {isOwnComment(reply) && !reply.isDeleted && (
                      <>
                        <button
                          className="comment-action-btn edit"
                          onClick={() => handleStartEdit(reply)}
                        >
                           
                        </button>
                        <button
                          className="comment-action-btn delete"
                          onClick={() => handleDeleteComment(reply.id)}
                        >
                           
                        </button>
                      </>
                    )}
                  </div>
                </div>
              ))}
            </div>
          ))
        )}
      </div>
    </div>
  );
}

export default CommentSection;
