import React, { useState, useEffect } from 'react';
import { useParams, useNavigate, useLocation } from 'react-router-dom';
import axios from 'axios';
import CommentSection from './CommentSection';
import './PostDetailPage.css';

function PostDetailPage() {
  const { boardId, postId } = useParams();
  const navigate = useNavigate();
  const location = useLocation();
  const board = location.state?.board;

  const [post, setPost] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchPost();
    incrementViewCount();
  }, [postId]);

  const fetchPost = async () => {
    try {
      const response = await axios.get(`/api/posts/${postId}`);
      setPost(response.data);
      setLoading(false);
    } catch (error) {
      console.error(':', error);
      // 
      const mockPost = {
        id: postId,
        title: '',
        content: `# 



## 
1. 
2. 
3. 
4. 

`,
        userId: 1,
        viewCount: 234,
        likeCount: 12,
        commentCount: 5,
        createdAt: '2026-01-20T10:30:00',
        updatedAt: '2026-01-20T10:30:00'
      };
      setPost(mockPost);
      setLoading(false);
    }
  };

  const incrementViewCount = async () => {
    try {
      // Get current user if logged in
      const user = JSON.parse(localStorage.getItem('user') || '{}');
      const userId = user.id || null;

      // Send user ID as query parameter
      const params = userId ? { userId } : {};
      await axios.post(`/api/posts/${postId}/view`, null, { params });
    } catch (error) {
      console.error(':', error);
    }
  };

  const handleLikePost = async () => {
    try {
      // Get current user if logged in
      const user = JSON.parse(localStorage.getItem('user') || '{}');
      const userId = user.id || null;

      // Send user ID as query parameter
      const params = userId ? { userId } : {};
      await axios.post(`/api/posts/${postId}/like`, null, { params });
      fetchPost();
    } catch (error) {
      console.error(':', error);
    }
  };

  const handleBack = () => {
    navigate(`/boards/${boardId}/posts`, { state: { board } });
  };

  const handleEdit = () => {
    navigate(`/boards/${boardId}/posts/${postId}/edit`, {
      state: { board, post }
    });
  };

  const handleDelete = async () => {
    if (!window.confirm('')) {
      return;
    }

    const user = JSON.parse(localStorage.getItem('user') || '{}');
    if (!user.id) {
      alert('');
      return;
    }

    try {
      await axios.delete(`/api/posts/${postId}?userId=${user.id}`);
      alert('');
      navigate(`/boards/${boardId}/posts`, { state: { board } });
    } catch (error) {
      console.error(':', error);
      if (error.response && error.response.status === 403) {
        alert('');
      } else {
        alert('');
      }
    }
  };

  const canModifyPost = () => {
    const user = JSON.parse(localStorage.getItem('user') || '{}');
    return user.id && post && user.id === post.userId;
  };

  const getFormattedDate = (dateString) => {
    if (!dateString) return '---';

    const date = new Date(dateString);

    // Check if date is valid
    if (isNaN(date.getTime())) return '---';

    return date.toLocaleString('ja-JP', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  if (loading) {
    return <div className="loading">...</div>;
  }

  if (!post) {
    return <div className="error"></div>;
  }

  return (
    <div className="post-detail-page">
      <div className="post-detail-container">
        <div className="post-detail-header">
          <button className="back-button" onClick={handleBack}>
             
          </button>
          <div className="header-right">
            <div className="board-badge">{board?.name || ''}</div>
            {canModifyPost() && (
              <>
                <button className="edit-button" onClick={handleEdit}>
                   
                </button>
                <button className="delete-button" onClick={handleDelete}>
                   
                </button>
              </>
            )}
          </div>
        </div>

        <div className="post-content-wrapper">
          <div className="post-header">
            <h1 className="post-title">{post.title}</h1>
            <div className="post-meta">
              <div className="meta-left">
                <span className="author">
                  {board?.isAnonymous ? post.anonymousId || '' : (post.authorNickname || 'Unknown User')}
                </span>
                <span className="separator"></span>
                <span className="date">{getFormattedDate(post.createdAt)}</span>
                {post.updatedAt && post.createdAt &&
                 new Date(post.updatedAt).getTime() - new Date(post.createdAt).getTime() > 1000 && (
                  <>
                    <span className="separator"></span>
                    <span className="edited">()</span>
                  </>
                )}
              </div>
              <div className="meta-right">
                <span className="stat"> {post.viewCount}</span>
                <span className="stat"> {post.likeCount}</span>
                <span className="stat"> {post.commentCount}</span>
              </div>
            </div>
          </div>

          <div className="post-body">
            <div className="post-content">
              {post.content.split('\n').map((line, index) => (
                <p key={index}>{line}</p>
              ))}
            </div>
          </div>

          <div className="post-actions">
            <button className="action-button like-button" onClick={handleLikePost}>
                ({post.likeCount})
            </button>
            <button className="action-button dislike-button">
                ({post.dislikeCount || 0})
            </button>
            <button className="action-button scrap-button">
               
            </button>
            <button className="action-button share-button">
               
            </button>
          </div>
        </div>

        <CommentSection
          postId={postId}
          boardId={boardId}
          isAnonymous={board?.isAnonymous || false}
        />
      </div>
    </div>
  );
}

export default PostDetailPage;
