import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import axios from 'axios';
import {
  AreaChart, Area, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer,
  PieChart, Pie, Cell
} from 'recharts';
import { FiUsers, FiShield, FiLayout, FiFileText, FiActivity, FiSettings } from 'react-icons/fi';
import './AdminPages.css';

// 
const useCountUp = (end, duration = 1000) => {
  const [count, setCount] = useState(0);

  useEffect(() => {
    if (end === 0) return;

    let startTime = null;
    const animate = (currentTime) => {
      if (startTime === null) startTime = currentTime;
      const progress = Math.min((currentTime - startTime) / duration, 1);
      setCount(Math.floor(progress * end));

      if (progress < 1) {
        requestAnimationFrame(animate);
      }
    };

    requestAnimationFrame(animate);
  }, [end, duration]);

  return count;
};

// 
const StatCard = ({ icon: Icon, title, value, trend, color, gradient }) => {
  const animatedValue = useCountUp(value || 0);

  return (
    <div className="stat-card" style={{ '--card-gradient': gradient }}>
      <div className="stat-icon" style={{ background: gradient }}>
        <Icon size={24} />
      </div>
      <div className="stat-content">
        <h3 className="stat-title">{title}</h3>
        <p className="stat-value">{animatedValue.toLocaleString()}</p>
        {trend !== undefined && trend !== 0 && (
          <span className={`stat-trend ${trend > 0 ? 'positive' : 'negative'}`}>
            {trend > 0 ? '' : ''} {Math.abs(trend)} 
          </span>
        )}
      </div>
    </div>
  );
};

// 
const getRelativeTime = (dateString) => {
  if (!dateString) return '';
  const date = new Date(dateString);
  const now = new Date();
  const diffMs = now - date;
  const diffMins = Math.floor(diffMs / 60000);
  const diffHours = Math.floor(diffMins / 60);
  const diffDays = Math.floor(diffHours / 24);

  if (diffMins < 1) return '';
  if (diffMins < 60) return `${diffMins}`;
  if (diffHours < 24) return `${diffHours}`;
  return `${diffDays}`;
};

function AdminPage() {
  const [stats, setStats] = useState(null);
  const [weeklyData, setWeeklyData] = useState([]);
  const [activities, setActivities] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [currentTime, setCurrentTime] = useState(new Date());
  const navigate = useNavigate();

  // 
  useEffect(() => {
    const timer = setInterval(() => setCurrentTime(new Date()), 1000);
    return () => clearInterval(timer);
  }, []);

  // 
  const checkAdminAccess = useCallback(() => {
    const user = JSON.parse(localStorage.getItem('user'));
    if (!user || user.role !== 'ADMIN') {
      navigate('/');
      return false;
    }
    return true;
  }, [navigate]);

  // 
  const fetchDashboardData = useCallback(async () => {
    try {
      const [statsRes, weeklyRes, activityRes] = await Promise.all([
        axios.get('/ej2/api/admin/dashboard', { withCredentials: true }),
        axios.get('/ej2/api/admin/dashboard/weekly', { withCredentials: true }),
        axios.get('/ej2/api/admin/dashboard/activity', { withCredentials: true })
      ]);

      setStats(statsRes.data);
      setWeeklyData(weeklyRes.data);
      setActivities(activityRes.data);
      setLoading(false);
    } catch (err) {
      console.error('Dashboard data fetch error:', err);
      setError('');
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    if (checkAdminAccess()) {
      fetchDashboardData();
    }
  }, [checkAdminAccess, fetchDashboardData]);

  // 
  const user = JSON.parse(localStorage.getItem('user'));

  // 
  const COLORS = ['#f6d365', '#667eea'];

  if (loading) {
    return (
      <div className="admin-dashboard">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="admin-dashboard">
        <div className="error-container">
          <p>{error}</p>
          <button onClick={fetchDashboardData}></button>
        </div>
      </div>
    );
  }

  return (
    <div className="admin-dashboard">
      {/*  */}
      <header className="dashboard-header">
        <div className="header-content">
          <div className="header-title">
            <h1></h1>
            <p className="header-welcome">{user?.name || ''}</p>
          </div>
          <div className="header-time">
            <span className="current-time">
              {currentTime.toLocaleTimeString('ja-JP')}
            </span>
            <span className="current-date">
              {currentTime.toLocaleDateString('ja-JP', {
                year: 'numeric',
                month: 'long',
                day: 'numeric',
                weekday: 'long'
              })}
            </span>
          </div>
        </div>
      </header>

      {/*  */}
      <section className="stats-section">
        <div className="stats-grid">
          <StatCard
            icon={FiUsers}
            title=""
            value={stats?.totalUsers}
            trend={stats?.newUsersThisWeek}
            gradient="linear-gradient(135deg, #667eea 0%, #764ba2 100%)"
          />
          <StatCard
            icon={FiShield}
            title=""
            value={stats?.adminCount}
            gradient="linear-gradient(135deg, #f6d365 0%, #fda085 100%)"
          />
          <StatCard
            icon={FiLayout}
            title=""
            value={stats?.totalBoards}
            gradient="linear-gradient(135deg, #11998e 0%, #38ef7d 100%)"
          />
          <StatCard
            icon={FiFileText}
            title=""
            value={stats?.totalPosts}
            trend={stats?.newPostsThisWeek}
            gradient="linear-gradient(135deg, #9D50BB 0%, #6E48AA 100%)"
          />
        </div>
      </section>

      {/*  */}
      <section className="charts-section">
        <div className="charts-grid">
          {/*  */}
          <div className="chart-card">
            <h3 className="chart-title">
              <FiActivity /> 
            </h3>
            <div className="chart-container">
              <ResponsiveContainer width="100%" height={250}>
                <AreaChart data={weeklyData}>
                  <defs>
                    <linearGradient id="colorUsers" x1="0" y1="0" x2="0" y2="1">
                      <stop offset="5%" stopColor="#667eea" stopOpacity={0.8}/>
                      <stop offset="95%" stopColor="#667eea" stopOpacity={0}/>
                    </linearGradient>
                    <linearGradient id="colorPosts" x1="0" y1="0" x2="0" y2="1">
                      <stop offset="5%" stopColor="#11998e" stopOpacity={0.8}/>
                      <stop offset="95%" stopColor="#11998e" stopOpacity={0}/>
                    </linearGradient>
                    <linearGradient id="colorComments" x1="0" y1="0" x2="0" y2="1">
                      <stop offset="5%" stopColor="#f6d365" stopOpacity={0.8}/>
                      <stop offset="95%" stopColor="#f6d365" stopOpacity={0}/>
                    </linearGradient>
                  </defs>
                  <CartesianGrid strokeDasharray="3 3" stroke="rgba(255,255,255,0.1)" />
                  <XAxis dataKey="name" stroke="rgba(255,255,255,0.7)" />
                  <YAxis stroke="rgba(255,255,255,0.7)" />
                  <Tooltip
                    contentStyle={{
                      background: 'rgba(30, 41, 59, 0.95)',
                      border: 'none',
                      borderRadius: '8px',
                      color: '#fff'
                    }}
                  />
                  <Area type="monotone" dataKey="users" stroke="#667eea" fillOpacity={1} fill="url(#colorUsers)" name="" />
                  <Area type="monotone" dataKey="posts" stroke="#11998e" fillOpacity={1} fill="url(#colorPosts)" name="" />
                  <Area type="monotone" dataKey="comments" stroke="#f6d365" fillOpacity={1} fill="url(#colorComments)" name="" />
                </AreaChart>
              </ResponsiveContainer>
            </div>
            <div className="chart-legend">
              <span className="legend-item"><span className="legend-dot" style={{background: '#667eea'}}></span></span>
              <span className="legend-item"><span className="legend-dot" style={{background: '#11998e'}}></span></span>
              <span className="legend-item"><span className="legend-dot" style={{background: '#f6d365'}}></span></span>
            </div>
          </div>

          {/*  */}
          <div className="chart-card">
            <h3 className="chart-title">
              <FiUsers /> 
            </h3>
            <div className="chart-container pie-chart-container">
              <ResponsiveContainer width="100%" height={250}>
                <PieChart>
                  <Pie
                    data={stats?.userDistribution || []}
                    cx="50%"
                    cy="50%"
                    innerRadius={60}
                    outerRadius={90}
                    paddingAngle={5}
                    dataKey="value"
                    animationBegin={0}
                    animationDuration={1000}
                  >
                    {(stats?.userDistribution || []).map((entry, index) => (
                      <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                    ))}
                  </Pie>
                  <Tooltip
                    contentStyle={{
                      background: 'rgba(30, 41, 59, 0.95)',
                      border: 'none',
                      borderRadius: '8px',
                      color: '#fff'
                    }}
                  />
                </PieChart>
              </ResponsiveContainer>
              <div className="pie-center-text">
                <span className="pie-total">{stats?.totalUsers || 0}</span>
                <span className="pie-label"></span>
              </div>
            </div>
            <div className="chart-legend">
              <span className="legend-item"><span className="legend-dot" style={{background: '#f6d365'}}></span></span>
              <span className="legend-item"><span className="legend-dot" style={{background: '#667eea'}}></span></span>
            </div>
          </div>
        </div>
      </section>

      {/*  */}
      <section className="bottom-section">
        <div className="bottom-grid">
          {/*  */}
          <div className="activity-card">
            <h3 className="card-title">
              <FiActivity /> 
            </h3>
            <div className="activity-list">
              {activities.length === 0 ? (
                <p className="no-activity"></p>
              ) : (
                activities.map((activity, index) => (
                  <div key={index} className="activity-item">
                    <span className="activity-icon">{activity.icon}</span>
                    <div className="activity-content">
                      <p className="activity-message">{activity.message}</p>
                      <span className="activity-time">{getRelativeTime(activity.time)}</span>
                    </div>
                  </div>
                ))
              )}
            </div>
          </div>

          {/*  */}
          <div className="quick-menu-card">
            <h3 className="card-title">
              <FiSettings /> 
            </h3>
            <div className="quick-menu-list">
              <Link to="/admin/users" className="quick-menu-item">
                <FiUsers />
                <span></span>
                <span className="menu-arrow"></span>
              </Link>
              <Link to="/admin/boards" className="quick-menu-item">
                <FiLayout />
                <span></span>
                <span className="menu-arrow"></span>
              </Link>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
}

export default AdminPage;
