import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import axios from 'axios';
import { FiLayout, FiArrowLeft, FiPlus, FiEdit2, FiTrash2, FiX } from 'react-icons/fi';
import './AdminPages.css';

function AdminBoardsPage() {
  const [boards, setBoards] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [showModal, setShowModal] = useState(false);
  const [editingBoard, setEditingBoard] = useState(null);
  const [formData, setFormData] = useState({
    name: '',
    code: '',
    description: '',
    isAnonymous: false,
    requireAdmin: false
  });
  const navigate = useNavigate();

  // 
  const checkAdminAccess = useCallback(() => {
    const user = JSON.parse(localStorage.getItem('user'));
    if (!user || user.role !== 'ADMIN') {
      navigate('/');
      return false;
    }
    return true;
  }, [navigate]);

  // 
  const fetchBoards = useCallback(async () => {
    try {
      const response = await axios.get('/ej2/api/admin/boards', {
        withCredentials: true
      });
      setBoards(response.data);
      setLoading(false);
    } catch (err) {
      console.error('Failed to fetch boards:', err);
      setError('');
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    if (checkAdminAccess()) {
      fetchBoards();
    }
  }, [checkAdminAccess, fetchBoards]);

  // 
  const resetForm = () => {
    setFormData({
      name: '',
      code: '',
      description: '',
      isAnonymous: false,
      requireAdmin: false
    });
  };

  // 
  const openCreateModal = () => {
    setEditingBoard(null);
    resetForm();
    setShowModal(true);
  };

  // 
  const openEditModal = (board) => {
    setEditingBoard(board);
    setFormData({
      name: board.name || '',
      code: board.code || '',
      description: board.description || '',
      isAnonymous: board.isAnonymous || false,
      requireAdmin: board.requireAdmin || false
    });
    setShowModal(true);
  };

  // 
  const closeModal = () => {
    setShowModal(false);
    setEditingBoard(null);
    resetForm();
  };

  // 
  const handleSubmit = async (e) => {
    e.preventDefault();

    if (!formData.name.trim() || !formData.code.trim()) {
      alert('');
      return;
    }

    try {
      if (editingBoard) {
        await axios.put(`/ej2/api/admin/boards/${editingBoard.id}`, formData, {
          withCredentials: true
        });
      } else {
        await axios.post('/ej2/api/admin/boards', formData, {
          withCredentials: true
        });
      }
      closeModal();
      fetchBoards();
    } catch (err) {
      alert(editingBoard ? '' : '');
    }
  };

  // 
  const handleDelete = async (boardId, boardName) => {
    if (!window.confirm(`${boardName}\n`)) return;

    try {
      await axios.delete(`/ej2/api/admin/boards/${boardId}`, {
        withCredentials: true
      });
      fetchBoards();
    } catch (err) {
      alert('');
    }
  };

  if (loading) {
    return (
      <div className="admin-page">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="admin-page">
      {/*  */}
      <header className="page-header">
        <Link to="/admin" className="back-link">
          <FiArrowLeft /> 
        </Link>
        <h1><FiLayout /> </h1>
        <button className="btn-primary" onClick={openCreateModal}>
          <FiPlus /> 
        </button>
      </header>

      {error && <div className="error-message">{error}</div>}

      {/*  */}
      <div className="table-container">
        <table className="admin-table">
          <thead>
            <tr>
              <th>ID</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {boards.length === 0 ? (
              <tr>
                <td colSpan="7" className="empty-message"></td>
              </tr>
            ) : (
              boards.map(board => (
                <tr key={board.id}>
                  <td className="td-id">{board.id}</td>
                  <td className="td-code">{board.code}</td>
                  <td>{board.name}</td>
                  <td className="td-description">{board.description || '-'}</td>
                  <td>
                    <span className={`badge ${board.isAnonymous ? 'badge-yes' : 'badge-no'}`}>
                      {board.isAnonymous ? 'ON' : 'OFF'}
                    </span>
                  </td>
                  <td>
                    <span className={`badge ${board.requireAdmin ? 'badge-yes' : 'badge-no'}`}>
                      {board.requireAdmin ? 'ON' : 'OFF'}
                    </span>
                  </td>
                  <td className="td-actions">
                    <button
                      className="btn-edit btn-sm"
                      onClick={() => openEditModal(board)}
                      title=""
                    >
                      <FiEdit2 />
                    </button>
                    <button
                      className="btn-danger btn-sm"
                      onClick={() => handleDelete(board.id, board.name)}
                      title=""
                    >
                      <FiTrash2 />
                    </button>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      {/*  */}
      {showModal && (
        <div className="modal-overlay" onClick={closeModal}>
          <div className="modal" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h2>{editingBoard ? '' : ''}</h2>
              <button className="modal-close" onClick={closeModal}>
                <FiX />
              </button>
            </div>
            <form onSubmit={handleSubmit}>
              <div className="form-group">
                <label> <span className="required">*</span></label>
                <input
                  type="text"
                  value={formData.code}
                  onChange={(e) => setFormData({...formData, code: e.target.value})}
                  placeholder=": free, notice"
                  required
                />
                <small>URL</small>
              </div>
              <div className="form-group">
                <label> <span className="required">*</span></label>
                <input
                  type="text"
                  value={formData.name}
                  onChange={(e) => setFormData({...formData, name: e.target.value})}
                  placeholder=": "
                  required
                />
              </div>
              <div className="form-group">
                <label></label>
                <textarea
                  value={formData.description}
                  onChange={(e) => setFormData({...formData, description: e.target.value})}
                  placeholder=""
                  rows="3"
                />
              </div>
              <div className="form-group checkbox-group">
                <label className="checkbox-label">
                  <input
                    type="checkbox"
                    checked={formData.isAnonymous}
                    onChange={(e) => setFormData({...formData, isAnonymous: e.target.checked})}
                  />
                  <span></span>
                </label>
                <small></small>
              </div>
              <div className="form-group checkbox-group">
                <label className="checkbox-label">
                  <input
                    type="checkbox"
                    checked={formData.requireAdmin}
                    onChange={(e) => setFormData({...formData, requireAdmin: e.target.checked})}
                  />
                  <span></span>
                </label>
                <small></small>
              </div>
              <div className="modal-buttons">
                <button type="button" className="btn-secondary" onClick={closeModal}>
                  
                </button>
                <button type="submit" className="btn-primary">
                  {editingBoard ? '' : ''}
                </button>
              </div>
            </form>
          </div>
        </div>
      )}
    </div>
  );
}

export default AdminBoardsPage;
