import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import axios from 'axios';
import { FiUsers, FiArrowLeft, FiTrash2, FiShield, FiUser } from 'react-icons/fi';
import './AdminPages.css';

function AdminUsersPage() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [page, setPage] = useState(0);
  const [error, setError] = useState('');
  const navigate = useNavigate();

  // 
  const checkAdminAccess = useCallback(() => {
    const user = JSON.parse(localStorage.getItem('user'));
    if (!user || user.role !== 'ADMIN') {
      navigate('/');
      return false;
    }
    return true;
  }, [navigate]);

  // 
  const fetchUsers = useCallback(async () => {
    try {
      const response = await axios.get(`/ej2/api/admin/users?page=${page}&size=20`, {
        withCredentials: true
      });
      setUsers(response.data);
      setLoading(false);
    } catch (err) {
      console.error('Failed to fetch users:', err);
      setError('');
      setLoading(false);
    }
  }, [page]);

  useEffect(() => {
    if (checkAdminAccess()) {
      fetchUsers();
    }
  }, [checkAdminAccess, fetchUsers]);

  // 
  const handleRoleChange = async (userId, newRole) => {
    const currentUser = JSON.parse(localStorage.getItem('user'));
    if (currentUser.id === userId) {
      alert('');
      return;
    }

    if (!window.confirm(` ${newRole} `)) return;

    try {
      await axios.put(`/ej2/api/admin/users/${userId}/role`,
        { role: newRole },
        { withCredentials: true }
      );
      fetchUsers();
    } catch (err) {
      alert('');
    }
  };

  // 
  const handleDeleteUser = async (userId, userName) => {
    const currentUser = JSON.parse(localStorage.getItem('user'));
    if (currentUser.id === userId) {
      alert('');
      return;
    }

    if (!window.confirm(`${userName}`)) return;

    try {
      await axios.delete(`/ej2/api/admin/users/${userId}`, { withCredentials: true });
      fetchUsers();
    } catch (err) {
      alert('');
    }
  };

  // 
  const formatDate = (dateString) => {
    if (!dateString) return '-';
    return new Date(dateString).toLocaleDateString('ja-JP', {
      year: 'numeric',
      month: '2-digit',
      day: '2-digit'
    });
  };

  if (loading) {
    return (
      <div className="admin-page">
        <div className="loading-container">
          <div className="loading-spinner"></div>
          <p>...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="admin-page">
      {/*  */}
      <header className="page-header">
        <Link to="/admin" className="back-link">
          <FiArrowLeft /> 
        </Link>
        <h1><FiUsers /> </h1>
      </header>

      {error && <div className="error-message">{error}</div>}

      {/*  */}
      <div className="table-container">
        <table className="admin-table">
          <thead>
            <tr>
              <th>ID</th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
              <th></th>
            </tr>
          </thead>
          <tbody>
            {users.map(user => (
              <tr key={user.id}>
                <td className="td-id">{user.id}</td>
                <td className="td-username">{user.username}</td>
                <td>{user.name}</td>
                <td className="td-email">{user.email}</td>
                <td>
                  <select
                    value={user.role || 'USER'}
                    onChange={(e) => handleRoleChange(user.id, e.target.value)}
                    className={`role-select ${user.role === 'ADMIN' ? 'admin' : 'user'}`}
                  >
                    <option value="USER">USER</option>
                    <option value="ADMIN">ADMIN</option>
                  </select>
                </td>
                <td className="td-date">{formatDate(user.createdAt)}</td>
                <td>
                  <button
                    className="btn-danger btn-sm"
                    onClick={() => handleDeleteUser(user.id, user.name)}
                    title=""
                  >
                    <FiTrash2 />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {/*  */}
      <div className="pagination">
        <button
          className="btn-secondary"
          onClick={() => setPage(Math.max(0, page - 1))}
          disabled={page === 0}
        >
          
        </button>
        <span className="page-info"> {page + 1}</span>
        <button
          className="btn-secondary"
          onClick={() => setPage(page + 1)}
          disabled={users.length < 20}
        >
          
        </button>
      </div>

      {/*  */}
      <div className="role-legend">
        <span className="legend-item">
          <FiShield className="icon-admin" /> ADMIN = 
        </span>
        <span className="legend-item">
          <FiUser className="icon-user" /> USER = 
        </span>
      </div>
    </div>
  );
}

export default AdminUsersPage;
