import React, { useState } from 'react';
import axios from 'axios';
import './AuthPages.css';

/**
 *  / 
 */
function FindAccountPage() {
  const [activeTab, setActiveTab] = useState('username'); // 'username' or 'password'

  //   
  const [findEmail, setFindEmail] = useState('');
  const [findName, setFindName] = useState('');
  const [findResult, setFindResult] = useState('');
  const [findError, setFindError] = useState('');
  const [findLoading, setFindLoading] = useState(false);

  //   
  const [step, setStep] = useState(1);
  const [resetEmail, setResetEmail] = useState('');
  const [token, setToken] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [resetError, setResetError] = useState('');
  const [resetSuccess, setResetSuccess] = useState('');
  const [resetLoading, setResetLoading] = useState(false);

  //   
  const handleFindUsername = async (e) => {
    e.preventDefault();
    setFindError('');
    setFindResult('');
    setFindLoading(true);

    try {
      const response = await axios.post('/api/auth/find-username', {
        email: findEmail,
        name: findName
      });

      if (response.data.success) {
        setFindResult(response.data.message);
      } else {
        setFindError(response.data.message);
      }
    } catch (err) {
      if (err.response && err.response.data) {
        setFindError(err.response.data.message || '');
      } else {
        setFindError('');
      }
    } finally {
      setFindLoading(false);
    }
  };

  //   
  const handleRequestReset = async (e) => {
    e.preventDefault();
    setResetError('');
    setResetSuccess('');
    setResetLoading(true);

    try {
      const response = await axios.post('/api/auth/password-reset/request', {
        email: resetEmail
      });

      if (response.data.success) {
        setResetSuccess(response.data.message);
        setStep(2);
      } else {
        setResetError(response.data.message);
      }
    } catch (err) {
      if (err.response && err.response.data) {
        setResetError(err.response.data.message || '');
      } else {
        setResetError('');
      }
    } finally {
      setResetLoading(false);
    }
  };

  //   
  const handleConfirmReset = async (e) => {
    e.preventDefault();
    setResetError('');
    setResetSuccess('');

    if (newPassword !== confirmPassword) {
      setResetError('');
      return;
    }

    if (newPassword.length < 6) {
      setResetError('6');
      return;
    }

    setResetLoading(true);

    try {
      const response = await axios.post('/api/auth/password-reset/confirm', {
        token: token,
        newPassword: newPassword
      });

      if (response.data.success) {
        setResetSuccess('');
        setTimeout(() => {
          window.location.href = '/login';
        }, 2000);
      } else {
        setResetError(response.data.message);
      }
    } catch (err) {
      if (err.response && err.response.data) {
        setResetError(err.response.data.message || '');
      } else {
        setResetError('');
      }
    } finally {
      setResetLoading(false);
    }
  };

  //     
  const handleTabChange = (tab) => {
    setActiveTab(tab);
    //   
    setFindEmail('');
    setFindName('');
    setFindResult('');
    setFindError('');
    //   
    setStep(1);
    setResetEmail('');
    setToken('');
    setNewPassword('');
    setConfirmPassword('');
    setResetError('');
    setResetSuccess('');
  };

  return (
    <div className="auth-container">
      <div className="auth-card">
        <h2 className="auth-title"></h2>

        {/*   */}
        <div className="find-tabs">
          <button
            className={`find-tab ${activeTab === 'username' ? 'active' : ''}`}
            onClick={() => handleTabChange('username')}
          >
            ID
          </button>
          <button
            className={`find-tab ${activeTab === 'password' ? 'active' : ''}`}
            onClick={() => handleTabChange('password')}
          >
            
          </button>
        </div>

        {/*    */}
        {activeTab === 'username' && (
          <>
            {findError && <div className="auth-error">{findError}</div>}
            {findResult && <div className="auth-success">{findResult}</div>}

            <form onSubmit={handleFindUsername} className="auth-form">
              <p className="auth-description">
                
              </p>

              <div className="form-group">
                <label htmlFor="findName"></label>
                <input
                  type="text"
                  id="findName"
                  value={findName}
                  onChange={(e) => setFindName(e.target.value)}
                  required
                  placeholder=""
                  disabled={findLoading}
                />
              </div>

              <div className="form-group">
                <label htmlFor="findEmail"></label>
                <input
                  type="email"
                  id="findEmail"
                  value={findEmail}
                  onChange={(e) => setFindEmail(e.target.value)}
                  required
                  placeholder=""
                  disabled={findLoading}
                />
              </div>

              <button
                type="submit"
                className="auth-button"
                disabled={findLoading}
              >
                {findLoading ? '...' : 'ID'}
              </button>
            </form>
          </>
        )}

        {/*    */}
        {activeTab === 'password' && (
          <>
            {resetError && <div className="auth-error">{resetError}</div>}
            {resetSuccess && <div className="auth-success">{resetSuccess}</div>}

            {step === 1 ? (
              <form onSubmit={handleRequestReset} className="auth-form">
                <p className="auth-description">
                  
                </p>

                <div className="form-group">
                  <label htmlFor="resetEmail"></label>
                  <input
                    type="email"
                    id="resetEmail"
                    value={resetEmail}
                    onChange={(e) => setResetEmail(e.target.value)}
                    required
                    placeholder=""
                    disabled={resetLoading}
                  />
                </div>

                <button
                  type="submit"
                  className="auth-button"
                  disabled={resetLoading}
                >
                  {resetLoading ? '...' : ''}
                </button>
              </form>
            ) : (
              <form onSubmit={handleConfirmReset} className="auth-form">
                <p className="auth-description">
                  
                </p>

                <div className="form-group">
                  <label htmlFor="token"></label>
                  <input
                    type="text"
                    id="token"
                    value={token}
                    onChange={(e) => setToken(e.target.value)}
                    required
                    placeholder=""
                    disabled={resetLoading}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="newPassword"></label>
                  <input
                    type="password"
                    id="newPassword"
                    value={newPassword}
                    onChange={(e) => setNewPassword(e.target.value)}
                    required
                    placeholder="6"
                    disabled={resetLoading}
                  />
                </div>

                <div className="form-group">
                  <label htmlFor="confirmPassword"></label>
                  <input
                    type="password"
                    id="confirmPassword"
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    required
                    placeholder=""
                    disabled={resetLoading}
                  />
                </div>

                <button
                  type="submit"
                  className="auth-button"
                  disabled={resetLoading}
                >
                  {resetLoading ? '...' : ''}
                </button>
              </form>
            )}
          </>
        )}

        <div className="auth-links">
          <a href="/login"></a>
        </div>
      </div>
    </div>
  );
}

export default FindAccountPage;
