import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import './BoardListPage.css';

function BoardListPage() {
  const [boards, setBoards] = useState([]);
  const [loading, setLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    fetchBoards();
  }, []);

  const fetchBoards = async () => {
    try {
      const response = await axios.get('/api/boards');
      setBoards(response.data);
      setLoading(false);
    } catch (error) {
      console.error(':', error);
      // 
      const mockBoards = [
        { id: 1, code: 'GENERAL', name: '', description: '', isAnonymous: false },
        { id: 2, code: 'ANONYMOUS', name: '', description: '', isAnonymous: true },
        { id: 3, code: 'EVENT', name: '', description: '', isAnonymous: false },
        { id: 4, code: 'MARKET', name: '', description: '', isAnonymous: false },
        { id: 5, code: 'BEST', name: '', description: '', isAnonymous: false },
      ];
      setBoards(mockBoards);
      setLoading(false);
    }
  };

  const handleBoardClick = (board) => {
    navigate(`/boards/${board.id}/posts`, { state: { board } });
  };

  const getBoardIcon = (code) => {
    const icons = {
      'GENERAL': '',
      'ANONYMOUS': '',
      'EVENT': '',
      'MARKET': '',
      'BEST': ''
    };
    return icons[code] || '';
  };

  if (loading) {
    return <div className="loading">...</div>;
  }

  return (
    <div className="board-list-page">
      <div className="board-list-container">
        <div className="board-list-header">
          <h1></h1>
          <p className="board-list-subtitle"></p>
        </div>

        <div className="boards-grid">
          {boards.map(board => (
            <div
              key={board.id}
              className="board-card"
              onClick={() => handleBoardClick(board)}
            >
              <div className="board-icon">{getBoardIcon(board.code)}</div>
              <div className="board-info">
                <h3 className="board-name">
                  {board.name}
                  {board.isAnonymous && <span className="anonymous-badge"></span>}
                </h3>
                <p className="board-description">{board.description}</p>
              </div>
              <div className="board-arrow"></div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default BoardListPage;
